import '@/assets/css/bootstrap4.css';
import '@/assets/css/adminlte.min.css';

import {mapGetters, mapActions} from 'vuex';

export default {
    name: "ConsoleHeader",
    computed: mapGetters(['user', 'users']),
    methods: {
        ...mapActions(['fetchUsers', 'fetchGenerations', 'fetchUnits']),
        async logout (){
            await this.$store.dispatch('LogOut')
            this.$router.push('/console/login')
        }
    },
    created(){
        if(this.user !== null){
            this.fetchUsers();
            this.fetchGenerations();
            this.fetchUnits();
            this.$store.dispatch("setErrorNo", '');
            this.$store.dispatch("setMsg", '');
        }
    }
}
